.skills-container {

}

.skills-container {
    margin-left: 5%;
    margin-top: 10%;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
}

.skills-container > section > h1 {
    font-size: 2.5rem;
    color: #5792f2;
    margin: 0;
    padding-bottom: 5%;
}

.skills-container > section {
    width: 50%;

}

.skills-container > section > p > a {
    text-decoration: none;
    font-weight: bold;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.skills-container > section > p > a:hover {
    border-bottom: 1px solid white;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.react {
    color: #5792f2;
}

.spring {
    color: var(--green);
}

.skills-container > section > ul {
    list-style-type: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    width:70%;
    padding:0;
    margin-left:auto;
    margin-right:auto;
}

.skills-container > section > ul > li{
    width: 30%;
}

.skills-container > section > ul > li > div > button {
    margin-right:0.3rem;
    width:100%;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    padding: 0.4rem;
    font-size: 1rem;
    background: transparent;
    color: white;
    outline:none;
    border:1px solid white;
}

.skills-container > section > ul > li:nth-child(3) > div > button {
    border-right: 1px solid white;

}

.skills-container > section > ul > li:hover > div > button {
    cursor: pointer;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.skills-container > section > ul > li:first-child:hover > div > button, .skills-container > section > ul > li:first-child > div > button:focus {
    color: var(--green);
    border-color: var(--green);
}

.skills-container > section > ul > li:nth-child(2):hover > div > button, .skills-container > section > ul > li:nth-child(2) >div > button:focus {
    color: #5792f2;
    border-color:#5792f2;
}

.skills-container > section > ul > li:nth-child(3):hover > div > button, .skills-container > section > ul > li:nth-child(3) >div > button:focus  {
    color: #ff7f00;
    border-color:#ff7f00;
}


.skills-container-list{
    display:-ms-flexbox;
    display:flex;
    width:60%;
    margin-left:auto;
    margin-right:auto;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    text-align: center;
}

.skills-container-item{
    font-size:5rem;
    text-align:center;
    margin:0.5rem;
    width:40%;
    opacity:1;
    position:relative;
    -webkit-transition:0.2s all ease-in-out;
    -o-transition:0.2s all ease-in-out;
    transition:0.2s all ease-in-out;
}
.skills-container-items-section > ul > li{
    position: relative;
}

.skills-container-description{
    height:3rem;

    width:80%;
    margin-left:auto;
    margin-right:auto;
    text-align:center;
}

.popover{
    position:absolute;
    top:0;
    cursor:pointer;
    display:inline-block;
    width:300px;
    text-align:center;
    margin-top:-5rem;
    -webkit-transition:all 0.3s ease-in-out;
    -o-transition:all 0.3s ease-in-out;
    transition:all 0.3s ease-in-out;
    opacity:0;
}


.skills-container-item > span:before{
    opacity:1;
    -webkit-transition:all 0.3s linear;
    -o-transition:all 0.3s linear;
    transition:all 0.3s linear;
}

.skills-container-item > span:hover  a, .skills-container-item > span:focus  a {
    opacity:1;
    -webkit-transition:all 0.3s linear;
    -o-transition:all 0.3s linear;
    transition:all 0.3s linear;
}

.skills-container-item:before{
    -webkit-transition:all 0.3s linear;
    -o-transition:all 0.3s linear;
    transition:all 0.3s linear;
}

.skills-container-item > span:hover:before, .skills-container-item > span:focus:before {
    opacity:0;
    -webkit-transition:all 0.3s linear;
    -o-transition:all 0.3s linear;
    transition:all 0.3s linear;
}

.skills-container-item > span > a{
    text-decoration: none;
   content:attr(data-name);
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width:100%;
    height:100%;
    color:white;
    position: absolute;
    top:0;
    font-size:1.5rem;
    opacity:0;
    -webkit-transition:all 0.3s linear;
    -o-transition:all 0.3s linear;
    transition:all 0.3s linear;
    font-family: 'Raleway', sans-serif;

}

@media only screen and (max-width: 1024px){
    .skills-container > section > ul {
        width:100%;
    }

}

@media only screen and (max-width: 820px){

    .skills-container-description{
        padding-bottom:1rem;
    }

    .skills-container{
        -ms-flex-wrap:wrap;
            flex-wrap:wrap;
        margin-left:1rem;
        margin-right:1rem;
    }

    .skills-container > section > ul > li {
        width:33.3%;
    }

    .skills-container > section{
        width:100%;
    }


    .skills-container-items-section{
        border-top:1px solid white;
        padding-top:1rem;
        height:70%;
    }
}





