.projects-container{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
}

.projects-container-item{
    width:20%;
    min-height:100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-size:contain;
    -webkit-transition:0.2s linear all;
    -o-transition:0.2s linear all;
    transition:0.2s linear all;
    cursor:pointer;
    position:relative;
}

.projects-container-item img{
    -webkit-transition:all 0.2s linear;
    -o-transition:all 0.2s linear;
    transition:all 0.2s linear;
    opacity:0.9;



}

.projects-container-item:hover img{
    opacity:0.2;
    -webkit-transition:0.2s linear all;
    -o-transition:0.2s linear all;
    transition:0.2s linear all;
}

.projects-container-item > a{
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    text-decoration:none;
    top:0;
    left:0;
    color:white;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack:center;
        align-content:center;
    width:100%;
    height:100%;
    font-size:3.5rem;

}

.projects-container-item > a > span{
    display:-ms-flexbox;
    display:flex;
    height:100%;
    width:100%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack:center;
        align-content:center;

}

.projects-container-item > a > span:before {
    text-align:center;
    width:100%;
    margin-left:0;
    position: absolute;
    display: block;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);

}

.projects-container-item > a > span:after{
    content:"VIEW PROJECT";
    padding-top:10%;
    font-size:1.3rem;
}

.projects-container-item > a:hover span{
    opacity:1;
    -webkit-transition:all 0.2s linear;
    -o-transition:all 0.2s linear;
    transition:all 0.2s linear;

}

.projects-container-item > a > span{
    opacity:0;
    -webkit-transition:all 0.2s linear;
    -o-transition:all 0.2s linear;
    transition:all 0.2s linear;
}

.projects-container-item  >  img{
    width:100%;
    height:100%;
}


@media only screen and (max-width: 1024px){
    .projects-container-item{
        width:50%;
    }

}

@media only screen and (max-width: 500px){
    .projects-container-item{
        width:100%;
    }
}



