:root{
    --blue:#265aaf;
    --green:#0b7c1c;
    --orange:#ff7f00;
    --yellow:#e0e014;
    font-size:17px;
}


body{
  background-color: #302e2e;
  overflow:hidden;
  font-family: 'Raleway', sans-serif;
}

#root{
    overflow:hidden;

}

.app {
  display:-ms-flexbox;
  display:flex;
  width:100%;
    min-height: 100vh;
    height:100%;
}

main{
    width:100%;
    height:100%;
}
nav{
    width:8%;
}
h1{
    font-weight:400;
}



@media only screen and (max-width: 1024px) {

    nav{
        width:15%;
    }

    body{
        overflow-y:scroll;
        overflow-x:hidden;
    }

}

@media only screen and (max-width: 750px) {

    .app {
        display: grid;
    }

    nav {
        width: 100%;
    }
}



@-moz-document url-prefix() {
    .sidebar > .sidebar-list > .sidebar-menu{
        padding-top:0;
    }

    .sidebar-list{
        flex-wrap:wrap;
        margin:0;
        height:100%;
        padding-top:1rem;
        padding-bottom:1rem;
    }

    .sidebar-list > .sidebar-social{
        display:none;
    }

    .sidebar-list >  .sidebar-menu > li{
        width:15%;
    }

    .sidebar-list > .sidebar-menu{
        width:100%;
    }

    .sidebar{
        justify-content: center;
    }

}


