.about-container{
    margin-left:5%;
    display:-ms-flexbox;
    display:flex;
    height:100%;
    -ms-flex-wrap:wrap;
    flex-wrap:wrap;
}

.about-container > section > h1{
    color:#5792f2;
    margin-top:10%;
    font-size:45px;
    font-size:2.5rem;
}

.about-container > section{
    margin-top:5%;
    width:50%;
}

.json-item{
    width:100%;
    padding-top:3.6px;
    padding-top:0.2rem;
    padding-bottom:3.6px;
    padding-bottom:0.2rem;
    padding-left:36px;
    padding-left:2rem;
    letter-spacing:1.8px;
    letter-spacing: 0.1rem;
}

.json-container{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-wrap:wrap;
    flex-wrap:wrap;
    -ms-flex-pack: center;
    justify-content: center;
}

.json{
    font-weight:400;
    font-size:19.8px;
    font-size:1.1rem;
    font-family: 'Quicksand', sans-serif;
    width:50%;
}

.json:before{
    font-size:25.2px;
    font-size:1.4rem;
    content:"{";
    color:var(--orange);
}

.json:after{
    font-size:25.2px;
    font-size:1.4rem;
    content:"}";
    color:var(--orange);

}

.json-value{
    color:var(--yellow);
}

.true{
    color:var(--green);
}

.json-key{
    color: var(--blue);
}

.json-number{
    color:#1499b7;
}

.image{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align:center;
    align-items:center;
}

.my-face{
    width:100%;
    height:100%;
}

@media only screen and (max-width: 750px) {

    .about-container > section{
        width:100%;
        margin-left:2.5%;
        margin-right:2.5%;
    }
    .about-container > .json-container{
        display:none;
    }

}


