.home-container{
    height:100%;
    margin-left:5%;
    font-size:3rem;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;

}
.home-welcome-text{
    font-weight: 200;
    width:100%;
    letter-spacing: 1px;
}

.home-welcome-text-small{
    font-weight: 200;
    width:100%;
    letter-spacing: 1px;
    -ms-flex-item-align:start;
        align-self:flex-start;
}

.home-welcome-text-small > div{
    display:inline-block;
}


@media only screen and (max-width: 750px){
    .home-container{
        text-align:center;
        padding-top:40%;
    }

}

@media only screen and (max-width: 450px){
    .home-welcome-text{
        font-size:5rem;
        color:var(--green);
    }
    .home-welcome-text-small{
        display:none;
    }

    .home-welcome-text-name{
        display:none;
    }
}


